.family_img {
  width: 80%;
  margin-left: 10%;
  margin-top: 100px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.about_content p {
  padding-left: 150px;
  padding-right: 150px;

  text-align: left;
  font-size: 18px;
  line-height: 40px;
}

.bold {
  font-weight: bold;
  font-size: 20px;
}

@media screen and (max-width: 768px) {
  .about_content p {
    margin-top: 50px;
    padding-right: 0;
    padding-left: 10%;
    max-width: 90%;
  }
}
